import { createRouter, createWebHistory } from 'vue-router'
import Home from '@/views/Home'
import contact from '@/views/contact'

const router = createRouter({
    history: createWebHistory(),
    routes: [
        {
            name: "Home",
            path: '/',
            component: Home
        },
        {
            name: "contact",
            path: '/contact',
            component: contact
        },
    ]
})


export default router;